import React from 'react'
import PureRenderMixin from 'react-addons-pure-render-mixin'
import {
    connect
} from 'react-redux'
import {
    bindActionCreators
} from 'redux'
import {
    timeFormat
} from 'utils/date.js'
import {update, sendMsg} from 'actions/userinfo.js'

import {
    BackTop,
    Popover,
    Row,
    Col,
    Progress
} from 'antd';
import './style.scss'

import Header from './subpage/Header/index.js'
import Content from './subpage/Content/';
import Loading from "components/Loading/index.js"
import Tables from 'components/Home/Tables/index.js'
import Footer from 'components/Platform/Footer/index.js'

import OpenLogo from 'images/open.png';
import SafeLogo from 'images/safe.png';
import EcologyLogo from 'images/ecology.png';
import Assetslogo from 'images/assetslogo.png';
import Block from 'images/block.png';
import Nodes from 'images/nodes.png';

class Forgot extends React.Component {
    constructor(props) {
        super(props);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.state = {
            allianceList: [{
                img: OpenLogo,
                charac: "",
                descList: ["", "", "BAAS"]
            }, {
                img: SafeLogo,
                charac: "",
                descList: ["", "ECDSA", ""]
            }, {
                img: EcologyLogo,
                charac: "",
                descList: ["", ""]
            }]
        };
    }

    login = () => {
        let loginState = this.props.userinfo.login;


        if (loginState) {
            this.props.userinfoActions.logout({
                login: false
            })
        } else {
            this.props.userinfoActions.login({
                login: true
            })
        }
    }

    getTableColumns = () => {
        const columns = [{
            title: '',
            dataIndex: 'node',
            key: 'node'
        }, {
            title: '',
            dataIndex: 'txid',
            key: 'txid',
        }, {
            title: '',
            dataIndex: 'txInfo',
            key: 'txInfo',
            render: (text, row, index) => {

                return (
                    <Popover placement="top" title={""} content={text} trigger="hover">
                        <span>{text.substr(0,50)}</span>
                    </Popover>
                );
            },
        }, {
            title: '',
            dataIndex: 'block',
            key: 'block',
        }, {
            title: '',
            dataIndex: 'seconds',
            key: 'seconds',
            render: (text, row, index) => {
                return timeFormat(text);
            }
        }];

        return columns;

    }

    sendMsg = (data) => {
        this.props.sendMsg(data).then((res) => {
            console.log(res.data.code)
        }).catch((err) => {
            console.log(err)
        })
    }

    update = (data) => {
        this.props.update(data)
    }

    componentWillMount() {

    }

    componentDidMount() {

    }

    render() {

        const percent = Number((this.props.homesData.issuedAmt / this.props.homesData.totalAmt) * 100).toFixed(0) + "%";

        const nowURTStyle = {
            marginLeft: percent
        }

        return (
            <div>
                <Header />
                <Content
                    update={this.update}
                    sendMsg = {this.sendMsg}
                />
                <Footer />
                <BackTop>
                    <div className="ant-back-top-inner">UP</div>
                </BackTop>
                {/* <Loading /> */}
            </div>

        )
    }
}

function mapStateToProps(state) {
    return {
        homesData: state.homesData
    }
}

function mapDispatchToProps(dispatch) {
    return {
        update: bindActionCreators(update, dispatch),
        sendMsg: bindActionCreators(sendMsg, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Forgot)


